import React from 'react'
import {useEffect,useState} from 'react'

const Nav = ()=>{
    const [fix,setFix] = useState(false);
    const [active,setActive] = useState(0);
    const [show,setShow] = useState(false);
    useEffect(() => {
        $(document).ready(function(){
            console.log('ready')
            let scr = false;
            let ab = document.querySelector('#about').offsetTop - 60;
            let ico = document.querySelector("#ico").offsetTop - 60;
            let rm = document.querySelector("#rm").offsetTop - 60;
            let at = 0;
            window.addEventListener("scroll",(e)=>{
                let w = window.pageYOffset;
                switch (true){
                    case w < ab && at != 0:
                    at = 0;
                    setActive(0);
                    break;
                    case w >= ab && w < ico && at != 1:
                    at = 1;
                    setActive(1);
                    break;
                    case w >= ico && w < rm && at != 2:
                    at = 2;
                    setActive(2);
                    break;
                    case w >= rm && at != 3:
                    at = 3;
                    setActive(3);
                }
                if(window.pageYOffset > 0) {
                    if (!scr){
                        setFix(true);
                        scr = true;
                    }
                } else {
                    if (scr){
                        setFix(false);
                        scr = false;
                    }
                }
            })
            $(document).on('click', 'a[href^="#"]', function(e) {
                // target element id
                var id = $(this).attr('href');

                // target element
                var $id = $(id);
                if ($id.length === 0) {
                    return;
                }

                // prevent standard hash navigation (avoid blinking in IE)
                e.preventDefault();

                // top position relative to the document
                var pos = $id.offset().top;

                // animated top scrolling
                $('body, html').animate({scrollTop: pos});
            });
        })
        
        return () => {

        };
    }, [null])
    return (
        <div className={`flex items-center ${fix?'bg-green-500 shadow fixed pin-t pin-l w-full z-20':''}`} style={{zIndex: 10,transition: "all .4s",height: fix?"60px":"91px"}}>
            <div className={`container mx-auto text-white px-4`}>
                <div className="flex items-center justify-between">
                    <a href="" className="">
                        <img src="/static/img/logo.png" alt="" className={`${fix?'w-48':'w-64'}`}/>
                    </a>            
                    <div className="hidden lg:block">
                        <div className="flex">
                            <a href="#" className={`font-bold h-12 px-4 flex items-center ${active===0?'bg-gray-700':''}`}>Home</a>
                            <a href="#about" className={`font-bold h-12 px-4 flex items-center ${active===1?'bg-gray-700':''}`}>About US</a>
                            <a href="#ico" className={`font-bold h-12 px-4 flex items-center ${active===2?'bg-gray-700':''}`}>Ecosystem</a>
                            <a href="#rm" className={`font-bold h-12 px-4 flex items-center ${active===3?'bg-gray-700':''}`}>Roadmap</a>
                            <a href="https://gfttoken.com/login" className={`font-bold h-12 px-4 flex items-center ${active===3?'bg-gray-700':''}`}>Login</a>
                            <a href="https://gfttoken.com/register" className={`font-bold h-12 px-4 flex items-center ${active===3?'bg-gray-700':''}`}>Register</a>
                        </div>
                    </div>
                    <div className="flex items-center lg:hidden">
                        <button className="w-8 h-8 inline-block" onClick={()=>setShow(true)}>
                            <svg className="w-8 h-8" viewBox="0 0 448 512">
                                <path fill="currentColor" d="M16 132h416c8.837 0 16-7.163 16-16V76c0-8.837-7.163-16-16-16H16C7.163 60 0 67.163 0 76v40c0 8.837 7.163 16 16 16zm0 160h416c8.837 0 16-7.163 16-16v-40c0-8.837-7.163-16-16-16H16c-8.837 0-16 7.163-16 16v40c0 8.837 7.163 16 16 16zm0 160h416c8.837 0 16-7.163 16-16v-40c0-8.837-7.163-16-16-16H16c-8.837 0-16 7.163-16 16v40c0 8.837 7.163 16 16 16z"></path>
                            </svg>
                        </button>
                    </div>
                    <div className={`fixed top-0 left-0 w-full h-full z-50 px-8 py-24 lg:hidden ${show?'':'hidden'}`} style={{backgroundColor: 'rgba(0,0,0,.9)'}}>
                        <div className="flex flex-col relative">
                            <div className="absolute top-0 right-0 -mt-16">
                                <button className="inline-block w-12 h-12" onClick={()=>setShow(false)}>
                                    <svg viewBox="0 0 352 512" className="w-12 h-12 text-red-500 fill-current">
                                        <path fill="currentColor" d="M242.72 256l100.07-100.07c12.28-12.28 12.28-32.19 0-44.48l-22.24-22.24c-12.28-12.28-32.19-12.28-44.48 0L176 189.28 75.93 89.21c-12.28-12.28-32.19-12.28-44.48 0L9.21 111.45c-12.28 12.28-12.28 32.19 0 44.48L109.28 256 9.21 356.07c-12.28 12.28-12.28 32.19 0 44.48l22.24 22.24c12.28 12.28 32.2 12.28 44.48 0L176 322.72l100.07 100.07c12.28 12.28 32.2 12.28 44.48 0l22.24-22.24c12.28-12.28 12.28-32.19 0-44.48L242.72 256z"></path>
                                    </svg>
                                </button>
                            </div>
                            <a onClick={()=>setShow(false)} href="#" className={`font-bold h-12 px-4 flex items-center ${active===0?'bg-gray-700':''}`}>Home</a>
                            <a onClick={()=>setShow(false)} href="#about" className={`font-bold h-12 px-4 flex items-center ${active===1?'bg-gray-700':''}`}>About US</a>
                            <a onClick={()=>setShow(false)} href="#ico" className={`font-bold h-12 px-4 flex items-center ${active===2?'bg-gray-700':''}`}>Ecosystem</a>
                            <a onClick={()=>setShow(false)} href="#rm" className={`font-bold h-12 px-4 flex items-center ${active===3?'bg-gray-700':''}`}>Roadmap</a>
                            <a href="#rm" className={`font-bold h-12 px-4 flex items-center`}>Login</a>
                            <a href="#rm" className={`font-bold h-12 px-4 flex items-center`}>Register</a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Nav